.wp-block-zolo-post-navigation {
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    position: relative;
    box-sizing: border-box;

    .zolo-item {
        text-decoration: none;
        display: flex;
        align-items: center;
        gap: 20px;
        flex-direction: row-reverse;
        justify-content: space-between;
        width: 100%;
        box-sizing: border-box;
        &:hover {
            .zolo-image-wrap {
                img {
                    transform: scale(1.1);
                }
            }

            .zolo-pos-nav-title {
                color: #006aff;
            }
        }

        &.zolo-post-next {
            .zolo-content-wrap {
                text-align: right;
            }
        }

        &.zolo-post-prev {
            .zolo-nav-text {
                flex-direction: row-reverse;
            }
        }
    }
    .zolo-content-wrap {
        width: 100%;
    }

    .zolo-image-wrap {
        overflow: hidden;
        border-radius: 6px;
        display: none;
        width: 180px;

        img {
            width: 100%;
            height: auto;
            vertical-align: middle;
            transition: transform 0.3s ease;
        }
    }
    .zolo-nav-text {
        font-size: clamp(10px, 0.8vw, 12px);
        color: #006aff;
        display: inline-block;
        border: 1px solid rgba(26, 25, 26, 0.11);
        padding: 6px 10px;
        border-radius: 6px;
        font-weight: 700;
        display: inline-flex;
        align-items: center;
        gap: 6px;
        svg {
            width: 1em;
            height: 1em;
            display: block;
            transition: transform 0.3s ease;
            path {
                fill: currentColor;
            }
        }
    }

    .zolo-pos-nav-title {
        margin: clamp(10px, 1vw, 12px) 0 0;
        font-size: clamp(15px, 2vw, 18px);
        color: #2f2f35;
        transition: color 0.3s ease;
        font-weight: 500;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-inline-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
    }
}

@media (min-width: 768px) {
    .wp-block-zolo-post-navigation {
        .zolo-image-wrap {
            display: block;
        }

        .zolo-item {
            &.zolo-post-next {
                text-align: left;
            }
        }
    }
}

// post title animation type css start here

.zolo-post-title-type-1,
.zolo-post-title-type-2,
.zolo-post-title-type-3,
.zolo-post-title-type-4,
.zolo-post-title-type-5 {
    .zolo-pos-nav-title {
        background-repeat: no-repeat;
        transition:
            background-size 0.3s,
            color 0.2s !important;
        // display: inline;

        background-position: 0 100%;
        background-size: 0 100%;
    }
}

.zolo-post-title-type-1 {
    --zolo-post-title-type-primary-color: #000;
    .zolo-pos-nav-title {
        background-image: linear-gradient(var(--zolo-post-title-type-primary-color), var(--zolo-post-title-type-primary-color));
        background-position: 0 100%;
        background-size: 0 100%;
        &:hover {
            background-size: 100% 100%;
            color: #fff;
        }
    }
}

.zolo-post-title-type-2,
.zolo-post-title-type-3,
.zolo-post-title-type-4,
.zolo-post-title-type-5 {
    .zolo-pos-nav-title {
        background-image: linear-gradient(currentColor, currentColor);
        background-position: 0% 100%;
        background-size: 0% 2px;
        &:hover {
            background-size: 100% 2px;
        }
    }
}

.zolo-post-title-type-3 {
    .zolo-pos-nav-title {
        background-position: 50% 100%;
    }
}

.zolo-post-title-type-4 {
    .zolo-pos-nav-title {
        background-position: 0% 0;
    }
}

.zolo-post-title-type-5 {
    .zolo-pos-nav-title {
        background-position: 50% 0%;
    }
}
